import cx from "classnames";
import React, { useRef } from "react";

import { htmlEntities } from "@ilabs/ui";
import { ConfidenceLevel, IBlock } from "../types";

interface IProps {
  block: IBlock;
  blockId: string;
  onSelectBlock: () => void;
  selected: boolean;
  selections: { [selection: string]: ConfidenceLevel };
}

export const Block = ({ block, blockId, onSelectBlock, selected, selections }: IProps) => {
  const contents = useRef<HTMLDivElement>(null);
  const blockClasses = cx("block", { selected });
  let html = htmlEntities(block.text);
  for (const mark of Object.keys(selections)) {
    const split = html.split(mark);
    if (split.length <= 1) {
      continue;
    }
    const confidenceClass = cx({
      "high-confidence": selections[mark] === ConfidenceLevel.HIGH,
      "low-confidence": selections[mark] === ConfidenceLevel.LOW,
      "medium-confidence": selections[mark] === ConfidenceLevel.MEDIUM,
    });
    html = split.join(`<mark class="${confidenceClass}">${htmlEntities(mark)}</mark>`);
  }
  return (
    <div
      className={blockClasses}
      dangerouslySetInnerHTML={{ __html: html }}
      id={blockId}
      onClick={onSelectBlock}
      ref={contents}
    />
  );
};
Block.displayName = "Block";



