import cx from "classnames";
import moment from "moment";
import React, { useRef } from "react";

import { InnodomDateFormats, WorkbenchDateFormats } from "../../../constants";
import { StateCallbacks } from "../state";
import { getActiveDataPoint, getActiveValue } from "../state/util";
import {
  ConfidenceLevel,
  IDataPointGroup,
  IDataPointValue,
  IState,
  ITaxonomy,
  TaxonomyEntryType,
} from "../types";
import { Block } from "./Block";
import { TextSelection } from "./TextSelection";
import { useScrollToBlock } from "./useScrollToBlock";

interface IProps {
  state: IState;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}

interface ISelections {
  [idRef: string]: {
    [selection: string]: ConfidenceLevel;
  };
}
const extractSelections = (dataPoints: IState["dataPoints"]): ISelections => {
  const toReturn: ISelections = {};
  const dataPointKeys = Object.keys(dataPoints);
  let values: Array<IDataPointValue<any>> = [];
  for (const dataPoint of dataPointKeys) {
    if (dataPoints[dataPoint].type === TaxonomyEntryType.Selection) {
      values = values.concat(dataPoints[dataPoint].values as Array<IDataPointValue<any>>);
    } else if (dataPoints[dataPoint].type === TaxonomyEntryType.Group) {
      const group = dataPoints[dataPoint] as IDataPointGroup;
      for (const groupDataPoint of Object.keys(group.dataPoints)) {
        if (group.dataPoints[groupDataPoint].type !== TaxonomyEntryType.Selection) {
          continue;
        }
        for (const groupValues of group.values) {
          values = values.concat(groupValues[groupDataPoint]);
        }
      }
    }
  }
  for (const value of values) {
    if (value.idRef === null) {
      continue;
    }
    if (!(value.idRef in toReturn)) {
      toReturn[value.idRef] = {};
    }
    let confidence = ConfidenceLevel.LOW;
    if (value.confidence >= 0.6) {
      confidence = ConfidenceLevel.MEDIUM;
      if (value.confidence >= 0.8) {
        confidence = ConfidenceLevel.HIGH;
      }
    }
    toReturn[value.idRef][value.value] = confidence;
  }
  return toReturn;
};

const EMPTY_SELECTIONS = {};

export const Blocks = ({
  state,
  stateCallbacks: { onSelectBlock, onUpdateGroupedAnswer, onUpdateNonGroupedAnswer },
  taxonomy,
}: IProps) => {
  const scrollRef = useRef(null);
  useScrollToBlock(state, scrollRef);
  const wrapperClassName = cx("blocks", "h-100", "overflow-auto", "p-3", {
    selecting: state.activeDataPoint.groupKey !== "",
  });
  const activeDataPoint = getActiveDataPoint(state);
  const activeValue = getActiveValue(state);
  const selections = extractSelections(state.dataPoints);
  console.log(selections);
  return (
    <div className={wrapperClassName} ref={scrollRef}>
      <TextSelection
        onSelectText={text => {
          if (activeDataPoint !== null && activeValue !== null) {
            let value: any = null;
            let valueRaw: string = "";
            switch (activeDataPoint.type) {
              // we only support text selection for these types
              case TaxonomyEntryType.Number: {
                const numericValue = parseFloat(text.replace(/\s|\$|%|,/g, ""));
                if (!isNaN(numericValue)) {
                  value = numericValue;
                  valueRaw = numericValue.toString(10);
                }
                break;
              }
              case TaxonomyEntryType.Date:
              case TaxonomyEntryType.DateTime:
              case TaxonomyEntryType.Time: {
                const parsed = moment(text, WorkbenchDateFormats.allSupported);
                if (parsed.isValid()) {
                  value = parsed.toDate();
                  valueRaw = parsed.format(InnodomDateFormats[activeDataPoint.type]);
                }
                break;
              }
              case TaxonomyEntryType.Selection:
              case TaxonomyEntryType.String:
                value = text;
                valueRaw = text;
                break;
              default:
                return;
            }
            if (state.activeDataPoint.groupKey !== state.activeDataPoint.key) {
              onUpdateGroupedAnswer(
                state.activeDataPoint.groupKey,
                state.activeDataPoint.groupIndex,
                state.activeDataPoint.key,
                state.activeDataPoint.index,
                value,
                valueRaw,
              );
            } else {
              onUpdateNonGroupedAnswer(
                state.activeDataPoint.key,
                state.activeDataPoint.index,
                value,
                valueRaw,
              );
            }
          }
        }}
      >
        {state.blocks.map((block, i) => {
          const blockId = `block-${block.id}`;
          return (
            <Block
              block={block}
              blockId={blockId}
              key={blockId}
              onSelectBlock={() => {
                if (activeValue === null || activeValue.idRef === block.id) {
                  return;
                }
                if (activeValue.idRef === null) {
                  onSelectBlock(block.id, taxonomy, block.text);
                  return;
                }
                const confirmation = window.confirm(
                  "Are you sure you want to update the supporting text for this answer?",
                );
                if (confirmation) {
                  onSelectBlock(block.id, taxonomy, block.text);
                }
              }}
              selected={activeValue !== null && activeValue.idRef === block.id}
              selections={block.id in selections ? selections[block.id] : EMPTY_SELECTIONS}
            />
          );
        })}
      </TextSelection>
    </div>
  );
};
Blocks.displayName = "Blocks";



